.App {
  text-align: center;
}

.container {
  max-width: 1020px;
  padding: 1em 7.5em 0;
}

.App-header {
  background-color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
}

h2 {
  text-align: left;
}

ul.App-list {
  color: black;
  text-align: left;
}

li.App-list-item {
  list-style: none;
  margin: 1em 0.25em 1em 0;
}

.App-link {
  color: #61dafb;
}

@media (max-width: 900px) {
  .container {
    padding: 1em 4em;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 1em 2em;
  }
  ul.App-list {
    padding-left: 1em;
  }
}

@media (max-width: 380px) {
  .container {
    padding: 0 1em;
  }
}
